/* whole app */
html,
body {
  height: 100%;
  background-color:  #d7d7d8;
}
 body {
   font-family: 'Helvetica', Arial, sans-serif;  
} 

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.25rem;
}

p {
  font-size: 1rem;
}

a {
  /* display: contents; */
  color: black;
}

.section-title {
  text-align: center;
  margin-bottom: .2rem;
  color: gray;
  text-shadow: 0 0 3px #50b70c, 0 0 5px #f6ff00;
}

.section-sub-title {
  text-align: center;
  margin-bottom: .2rem;
  margin-top: 0rem;
  font-size: 1.2rem;
}

.card {
  width: 100%;
  margin-right: 60px;
  margin-left: 0px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 0px;
  background-color: white;
} 

.card-title {
  margin: 4px;
  text-align: center;
}

/* header component */
header {
  text-align: center;
  padding-top: 10px;
  background-color: #d7d7d8;
}

/* nav component */
#nav-list {
  padding: 8px;
  list-style: none;
  font-size: 1.4rem;
  margin-left: 7%;
  margin-right: 7%;
}

.navActive {
  color: gray;
  text-shadow: 0 0 3px #50b70c, 0 0 5px #f6ff00;
}

nav li {
  cursor: pointer;
}

/* about component */
#about-me-text {
  margin-top: 1rem;
}

#about-me-row {
  background-color: #86888a;
}

#about-me-card {
  background-color: #afafb1;
}

/* portfolio component */
/* no css on portfolio component */

/* project component */
#git-project-icon {
  margin-bottom: .2rem;
}

#git-repo-label {
  margin-top: 0%; 
  text-align: center;
} 

#project-img {
  margin-bottom: 3%;
  margin-top: 3%;
}

.project-img-overlay a {
  color: lightgray;
  text-shadow: 0 0 3px #50b70c, 0 0 5px #f6ff00; 
  text-decoration: underline;
}

.project-img-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0 ,0, 0.6);
  color: lightgray;
  text-shadow: 0 0 3px #50b70c, 0 0 5px #f6ff00;
  display: flex;
  flex-direction:column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: .8s;
}

.project-img-overlay > * {
  transform: translateY(120px);
  font-size: 0;
  /* height: 0; */
  /* display: inline; */
  transition: .8s;
}

.project-img-overlay:hover {
  box-shadow: 0 0 0 200px rgba(0,0,0, 0.6) inset;
  opacity: 1;
}

.project-img-overlay:hover > * {
  margin: .2rem;
  margin-left: .4rem;
  margin-right: .4rem;
  /* color: gray; */
  text-shadow: 0 0 3px #50b70c, 0 0 5px #f6ff00;
  /* display: contents; */
  transform: translateY(0);
}

.project-img-overlay:hover > h3 {
  font-size: 1.75rem;
}

.project-img-overlay:hover > p {
  font-size: .75rem;
}

.project-img-overlay:hover > a {
  font-size: .9rem;
}

#project-title {
  margin-bottom: 0%;
  text-align: center;
}

#project-card {
  background-color: #afafb1;
}

#portfolio-row {
  background-color: #86888a;
}

/* resume component */
.pdf {
   margin-left: 22%; 
   margin-bottom: 0rem;
}

#resume-div {
  margin-bottom: 1rem;
}

#resume-row {
  background-color: #86888a;
}

/* contact component */
#contact-form-label {
  margin-left: 8rem;
  width: 15rem;
}

input {
  width: 14rem;
  font-size: 1rem;
  padding: 0.25rem; 
  margin-top: 0rem;
  margin-bottom: 0rem;
  margin-left: 8rem;
  margin-right: 0rem;
  border: 2px solid #7e7c7c;
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
  display: flex;
}

input:focus {
  border: 1px solid var();
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

#contact-message-label {
   margin-left: 10rem;
}

textarea {
	width: 27rem;
	height: 6.5rem; 
	border: 2px solid #7e7c7c;
  border-radius: 3px;
	padding: 0.25rem; 
  margin-left: 10rem;
  margin-right: 10rem;
  display: flexbox;  
}

textarea:focus {
  border: 1px solid var();
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

#contact-form-container {
  padding: 0rem;
}

#contact-form-btn {
  margin-left: 30.9rem;
  width: 6rem; 
} 

#contact-form {
  margin-bottom: .6rem;
}

#contact-error-text {
  justify-items: end;
  margin-bottom: 0em;
}

#contact-row {
  background-color: #86888a;
}
#contact-card {
  background-color: #afafb1;
}

#contact-card-title {
  margin-top: .5rem;
}

#carousel-div {
 margin-bottom: .6rem;
} 

/* footer component */
footer {
  margin-left: 25%;
